import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import mixpanel from 'mixpanel-browser';

import arabic_easy from '../word/arabic_easy.json';
import english_easy from '../word/english_easy.json';
import korean_easy from '../word/korean_easy.json';
import japanese_easy from '../word/japanese_easy.json';

const WordMatchGame = ({level, language, difficulty}) => {
    const [gridColumns, setGridColumns] = useState(2);
    const [words, setWords] = useState([]);
    console.log('단어:', words)

    useEffect(() => {
        fetch(`${language}_${difficulty}`)
        .then(data => console.log('!:', `${language}_${difficulty}`))
        .then(data => setWords(`${language}_${difficulty}`.map(item => item.word)));

        // const selectSet = 
        // console.log('!: ', selectSet)
        // const wordList = selectSet.map(item => item.word);
        // setWords(wordList);
    }, []);

    const handleGridChange = (cols) => {
        setGridColumns(cols);
    };

    return (
        <Wrap>
            <TitleContainer>
                <Circle>{level}</Circle>
                <Circle>{language}</Circle>
                <Circle>{difficulty}</Circle>
            </TitleContainer>
            <ButtonContainer>
                {[2, 3, 4, 5].map((number) => (
                    <GridButton
                        key={number}
                        onClick={() => handleGridChange(number)}
                        selected={gridColumns === number}
                    >
                        {number}
                    </GridButton>
                ))}
            </ButtonContainer>
            <Board columns={gridColumns}>
                {words.map((word, index) => (
                    <WordCard key={index}>
                        {word}
                    </WordCard>
                ))}
            </Board>
        </Wrap>
    );
};

const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
`;

const TitleContainer = styled.div`
display: flex;
justify-content: center;
margin-bottom: 20px;
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
`;

const GridButton = styled.button`
  width: 40px;
  height: 40px;
  margin: 0 5px;
  border: 1px solid #ccc;
  background-color: ${(props) => (props.selected ? '#E2F5EB' : 'white')};
  cursor: pointer;
  &:hover {
    background-color: ${(props) => (props.selected ? '#E2F5EB' : 'lightgray')};
  }
`;

const Board = styled.div`
  display: grid;
  grid-template-columns: repeat(${(props) => props.columns}, 1fr);
  gap: 10px;
  width: 80%;
  border: 1px solid #eee;
  padding: 20px;
`;

const WordCard = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #ccc;
  padding: 20px;
  background-color: white;
  cursor: pointer;
  transition: background-color 0.3s;
  &:hover {
    background-color: lightgray;
  }
`;

const Circle = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  border-radius: 25px;
  margin: 5px;
  background-color: #E2F5EB;
`;

export default WordMatchGame;
